const $add = $("#add");
const $siteList = $(".siteList");
const removeHttp = (url) => {
  return url
    .replace("http://", "")
    .replace("https://", "")
    .replace("www.", "")
    .replace(/\/.*/, "");
};

const getLocalStorageItem = localStorage.getItem("stringHashTable");
const objectStringHashTable = JSON.parse(getLocalStorageItem);
//èŽ·å–å­˜å…¥çš„hashTable
let hashTable = objectStringHashTable || [{ siteLogo: "G", url: "google.com" }];
//ä½¿ç”¨æ•°ç»„ï¼ˆå†…æœ‰å¤šä¸ªå“ˆå¸Œè¡¨ï¼‰æ¥å­˜æ”¾åˆ›å»ºå‡ºæ¥çš„site

const render = () => {
  $siteList.find("li:not(#add)").remove();
  //æ¯æ¬¡æ¸²æŸ“æŠŠé™¤æœ€åŽä¸€ä¸ªä»¥å¤–éƒ½åˆ é™¤
  hashTable.forEach((node, index) => {
    const $li = $(`<li class="site longPressCanTouchDelete">
    <div class = 'close'>
    <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-close"></use>
              </svg>
              </div>
    <div class="siteLogo">
          ${node.siteLogo}
        </div>
        <div class="url">
        ${removeHttp(node.url)}
        </div>
      </li>
    `).insertBefore($add);
    //éåŽ†å“ˆå¸Œè¡¨ï¼Œä¸ºé‡Œé¢çš„å¯¹è±¡åˆ›å»ºli
    $li.on("click", () => {
      window.open("https://" + removeHttp(node.url), "_self");
    });
    //ç»™liæ·»åŠ ç‚¹å‡»äº‹ä»¶ï¼ˆpcç«¯ï¼‰
    $li.on("click", ".close", (event) => {
      event.stopPropagation();
      hashTable.splice(index, 1);
      render();
    });
    //åˆ é™¤äº‹ä»¶
    let longClick = 0;
    const $siteLogo = $li.find(".siteLogo");
    //ä¸èƒ½ç”¨$liç›‘å¬,å› ä¸ºå®ƒå·²ç»è¢«èµ‹äºˆä¸€ä¸ªç›‘å¬äº†
    $siteLogo.on({
      touchstart: function () {
        longClick = 0; //è®¾ç½®åˆå§‹ä¸º0
        timeOutEvent = setTimeout(function () {
          $li.find(".icon").css("visibility", "visible");
          //æ­¤å¤„ä¸ºé•¿æŒ‰äº‹ä»¶-----åœ¨æ­¤æ˜¾ç¤ºé®ç½©å±‚åŠåˆ é™¤æŒ‰é’®
          longClick = 1; //å‡å¦‚é•¿æŒ‰ï¼Œåˆ™è®¾ç½®ä¸º1
        }, 500);
      },
      touchmove: function () {
        clearTimeout(timeOutEvent);
        timeOutEvent = 0;
        e.preventDefault();
      },
      touchend: function () {
        clearTimeout(timeOutEvent);
        if (timeOutEvent != 0 && longClick == 0) {
          //ç‚¹å‡»
          //æ­¤å¤„ä¸ºç‚¹å‡»äº‹ä»¶----åœ¨æ­¤å¤„æ·»åŠ è·³è½¬è¯¦æƒ…é¡µ(ç§»åŠ¨ç«¯)
          window.open("https://" + removeHttp(node.url), "_self");
        }
        return false;
      },
    });

    //é•¿æŒ‰äº‹ä»¶
    $(document).on("click", () => {
      $li.find(".icon").css("visibility", "hidden");
    });
    // //åœ¨é•¿æŒ‰ç‚¹å‡»å…¶ä»–åœ°æ–¹ï¼Œåˆ é™¤æŒ‰é’®éšè—
  });
};

render();

$add.on("click", () => {
  const url = window.prompt("è¯·è¾“å…¥æ‚¨è¦æ·»åŠ çš„ç½‘å€");
  hashTable.push({
    siteLogo: removeHttp(url)[0].toUpperCase(),
    url: url,
  });
  render();
  //æ¯æ¬¡æ·»åŠ æ–°ç½‘å€é‡æ–°æ¸²æŸ“
  let stringHashTable = JSON.stringify(hashTable);
  let x = localStorage.setItem("stringHashTable", stringHashTable);
  //å°†hashTableå­˜å…¥localStorage
});

window.onbeforeunload = () => {
  let stringHashTable = JSON.stringify(hashTable);
  let x = localStorage.setItem("stringHashTable", stringHashTable);
};

$(document).on("keypress", (k) => {
  const { key } = k;

  for (let i = 0; i < hashTable.length; i++) {
    if (key.toUpperCase() === hashTable[i].siteLogo) {
      console.log("ok");
      window.open("https://" + hashTable[i].url, "_self");
    }
  }
});
//åŠ å…¥é”®ç›˜äº‹ä»¶
$(document).on("keypress", ".searchInput", (event) => {
  event.stopPropagation();
});
